.animal-weight h3 {
  font-size: calc(24px + (36 - 24) * ((100vw - 320px) / (1920 - 320)));
  line-height: 36px;
  color: #000;
  text-transform: uppercase;
}
.animal-weight a {
  padding: 23px 40px 23px 0;
  font-size: calc(18px + (20 - 18) * ((100vw - 320px) / (1920 - 320)));
  line-height: 29.64px;
  text-transform: uppercase;
  position: relative;
  font-weight: 400;
}
.animal-weight a::after {
  content: "";
  display: block;
  position: absolute;
  width: 32px;
  height: 10px;
  background: url("../img/Arrow.svg");
  top: 30px;
  right: -30px;
  transition: all 0.4s ease;
}
.animal-weight__wrapper a:hover::after {
  right: -45px;
}
.instructions {
  padding: 40px 0 40px;
}
.instructions__title {
  margin-bottom: 72px;
}
.instructions__list {
  display: flex;
  justify-content: space-between;
}
.instructions__item {
  margin: 0 40px;
}
.instructions__inner {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  min-height: 280px;
  padding-bottom: 57px;
  position: relative;
  cursor: grab;
  user-select: none;
}
.instructions__inner:hover .instructions__counter {
  color: #c2c2c2;
}
.instructions__container {
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
  position: relative;
  max-width: 1330px;
}
.instructions__counter {
  font-size: 280px;
  color: #f2f2f2;
  position: absolute;
  top: -106px;
  right: -10px;
  z-index: 1;
  transition: all 0.4s linear;
}
.instructions__icon {
  width: 80px;
  height: 80px;
  background-color: #68b738;
  position: relative;
  margin-bottom: 31px;
  z-index: 2;
}
.instructions__icon img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
}
.instructions__text {
  color: #444;
  font-family: "Arial";
  z-index: 2;
  position: relative;
  max-width: 235px;
}
.instructions__slider {
  position: relative;
}
.instructions__arrow {
  position: absolute;
  top: 85px;
  z-index: 5;
}
.instructions__arrow_left {
  left: -80px;
}
.instructions__arrow_right {
  right: -80px;
}
.slick-prev.slick-arrow {
  top: 85px;
  position: absolute;
  left: -80px;
  font-size: 0;
  width: 45px;
  height: 35px;
  background: url("../img/arrow-paw-left.svg") no-repeat;
}
.slick-next.slick-arrow {
  top: 85px;
  position: absolute;
  right: -80px;
  font-size: 0;
  width: 45px;
  height: 35px;
  background: url("../img/arrow-paw-right.svg") no-repeat;
}
.map {
  max-height: 400px;
}
.map iframe {
  width: 100%;
}
.result {
  padding: 30px 0 0;
}
.result h2 {
  padding: 30px 0 75px;
}
.result__info {
  background-color: #f2f2f2;
  padding: 66px 0 75px;
  position: relative;
}
.result p {
  color: #444;
  line-height: 24px;
  font-weight: 400;
  margin-bottom: 69px;
  max-width: 440px;
}
.result__data {
  display: flex;
  margin-bottom: 55px;
}
.result__quantity {
  display: block;
  text-transform: uppercase;
  color: #000;
  font-size: calc(24px + (30 - 24) * ((100vw - 320px) / (1920 - 320)));
  line-height: 37px;
  padding-top: 15px;
  padding-bottom: 25px;
  border: 1px #cdcdcd solid;
  border-radius: 5px;
}
.result__quantity_weight {
  padding-left: 57px;
  padding-right: 57px;
  margin-right: 42px;
}
.result__quantity_days {
  padding-left: 30px;
  padding-right: 30px;
}
.result__item {
  position: relative;
}
.result__subtitle {
  display: block;
  color: #757575;
  font-weight: 400;
  position: relative;
  top: -15px;
  background-color: #f2f2f2;
  text-align: center;
}
.result__subtitle_weight {
  width: 128px;
  right: -19px;
}
.result__subtitle_days {
  width: 136px;
  right: -13px;
}
.result__price-wrapper {
  display: flex;
}
.result__price-title {
  font-size: calc(18px + (20 - 18) * ((100vw - 320px) / (1920 - 320)));
  line-height: 20px;
  text-transform: uppercase;
  margin-right: 63px;
}
.result__price {
  font-size: calc(18px + (20 - 18) * ((100vw - 320px) / (1920 - 320)));
  line-height: 20px;
  text-transform: uppercase;
  margin-right: 40px;
  font-weight: 700;
}
.weight-results {
  position: absolute;
  top: -180px;
  right: 20%;
  min-width: 536px;
  min-height: 643px;
}
.weight-results img {
  max-height: 540px;
  left: 22px;
}
.weight-results__text {
  font-size: calc(18px + (20 - 18) * ((100vw - 320px) / (1920 - 320)));
  text-transform: uppercase;
  font-weight: bold;
  bottom: 50%;
}
.weight-results__before {
  position: absolute;
  left: 0;
}
.weight-results__after {
  position: absolute;
  right: 0;
}
.twentytwenty-handle {
  transform: rotate(90deg);
  top: initial;
  bottom: 30px;
  width: 34px;
  height: 34px;
  margin-left: 3px;
  border: 1px solid #cdcdcd;
  box-shadow: none;
  background-color: #fff;
}
.twentytwenty-left-arrow {
  display: none;
}
.twentytwenty-right-arrow {
  width: 8px;
  height: 8px;
  background-color: #68b738;
  border-radius: 100%;
  border: none;
  top: 18.7px;
  right: 28.1px;
}
.twentytwenty-horizontal .twentytwenty-handle:after,
.twentytwenty-horizontal .twentytwenty-handle:before {
  width: 6px;
  border-radius: 5px;
  background-color: #dcdcdc;
}
.twentytwenty-horizontal .twentytwenty-handle:before {
  bottom: 34%;
}
.twentytwenty-horizontal .twentytwenty-handle:after {
  top: 34%;
}
.twentytwenty-horizontal .twentytwenty-handle:after,
.twentytwenty-horizontal .twentytwenty-handle:before,
.twentytwenty-vertical .twentytwenty-handle:after,
.twentytwenty-vertical .twentytwenty-handle:before {
  box-shadow: none;
}
.footer {
  padding: 58px 0;
  background-color: #f2f2f2;
}
.footer__list {
  display: flex;
}
.footer__item:not(:last-of-type) {
  margin-right: 12px;
}
.footer__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.footer__link img {
  transition: all 0.1s ease;
  width: 40px;
  height: 40px;
  opacity: 0.6;
}
.footer__link:hover img {
  opacity: 1;
}
@media (max-width: 1480px) {
  .slick-prev.slick-arrow,
  .slick-next.slick-arrow {
    top: initial;
    bottom: -40px;
  }
  .slick-next.slick-arrow {
    right: 40%;
  }
  .slick-prev.slick-arrow {
    left: 40%;
  }
  .instructions {
    padding-bottom: 80px;
  }
}
@media (max-width: 1267px) {
  .weight-results {
    right: 6%;
  }
}
@media (max-width: 1200px) {
  .container {
    max-width: 960px;
  }
  .header__offer::after {
    display: none;
  }
  .menu__item a {
    color: #000;
  }
  .header__offer {
    z-index: 1;
  }
  .animal-weight__list {
    grid-template-columns: 1fr;
    justify-items: center;
  }
  .animal-weight__wrapper {
    width: 100%;
  }
  .weight-results {
    position: relative;
    top: 50px;
    right: 0;
    margin: 0 auto;
    max-width: 540px;
  }
}
@media (max-width: 992px) {
  .container {
    max-width: 90%;
  }
  .offer {
    display: block;
  }
  .header__offer {
    padding-top: 162px;
  }
  .offer__info {
    margin-bottom: 40px;
  }
  .header {
    padding-bottom: 0;
  }
  .header__container {
    position: static;
  }
}
@media (max-width: 864px) {
  .menu__list {
    position: absolute;
    top: -53px;
    right: 0;
    display: flex;
    flex-direction: column;
    background-color: #27782c;
    padding: 70px 35px 100px;
    border-bottom-left-radius: 35px;
    display: none;
    transition: all 0.4s ease;
    z-index: 3;
  }
  .burger.active + .menu__list {
    display: block;
  }
  .menu__list,
  .menu__item:not(:last-of-type) {
    margin-bottom: 40px;
  }
  .menu__item:not(:last-of-type) {
    margin-right: 0;
  }
  .menu__item a {
    color: #fff;
  }
  .burger {
    display: block;
  }
  .burger.active {
    z-index: 10;
    top: -32px;
    right: -19px;
  }
  .burger span.active {
    background-color: #fff;
  }
}
@media (max-width: 768px) {
  .offer__info {
    padding-left: 0;
  }
  .header__offer::after {
    display: block;
    width: 100%;
    top: 120px;
  }
  .offer {
    position: relative;
    z-index: 2;
    text-align: center;
  }
  .offer__title {
    max-width: 100%;
    color: #fff;
  }
  .offer__subtitle {
    color: #fff;
  }
  .header__inner {
    top: 33px;
  }
  .custom-logo-link {
    max-width: 190px;
  }
  .offer__btn {
    display: none;
  }
  .offer__btn_reverse {
    display: block;
    width: 100%;
  }
  .offer__info {
    margin-bottom: 0;
  }
  .instructions__title {
    text-align: center;
  }
  .result h2 {
    text-align: center;
  }
  .result p {
    max-width: initial;
  }
  .result__price-wrapper,
  .result__data {
    justify-content: center;
  }
  .weight-results img {
    max-width: 365px;
  }
  .weight-results__before {
    left: 85px;
  }
  .weight-results__after {
    right: 85px;
  }
  .weight-results__text {
    bottom: 75px;
  }
  .weight-results {
    max-width: 410px;
    min-width: 401px;
  }
}
@media (max-width: 576px) {
  .animal-weight__head {
    flex-wrap: wrap;
    justify-content: center;
  }
  .animal-weight__wrapper {
    padding: 0;
  }
  .animal-weight__list {
    grid-gap: 40px;
  }
  .animal-weight__icon {
    margin-bottom: 40px;
    margin-right: 0;
  }
  .animal-weight h3 {
    width: 100%;
    text-align: center;
  }
  .slick-next.slick-arrow {
    right: 20%;
  }
  .slick-prev.slick-arrow {
    left: 20%;
  }
  .instructions__item {
    margin: 0 15px;
  }
  .result__subtitle_days {
    right: -6px;
  }
  .result__data {
    flex-direction: column;
  }
  .result__quantity_weight {
    margin-right: 0;
  }
  .result__price-title {
    margin-right: 0;
    margin-bottom: 30px;
  }
  .result__price-wrapper {
    align-items: center;
    flex-direction: column;
  }
  .footer__inner {
    flex-direction: column;
    align-items: center;
  }
  .footer__list {
    margin-top: 40px;
  }
}
@media (max-width: 442px) {
  .weight-results {
    min-width: 300px;
    max-width: 314px;
    min-height: 480px;
  }
  .weight-results img {
    max-width: 275px;
  }
}
@media (orientation: landscape) and (max-width: 992px) {
  .offer__title {
    padding-bottom: 0;
  }
  .offer__input {
    margin-bottom: 15px;
  }
}
.ie9 body {
  background-color: #008000;
}
@media screen and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .jsMobile .burger {
    padding-right: 60px;
  }
}
